<script>
export default {
  name: "List",
  props : {
    ListSchools: {
      type: Array,
      default: () => []
    }
 },

  data() {
    return {
      textSearch: '',
    };
  },

  methods: {
    async getSchools() {
      return await this.$store.dispatch("uploadSchools/getListSchools");
    },

    async searchSchools() {
      await this.$store.commit("uploadSchools/set_textSearch", this.textSearch);
      clearTimeout(this.debounceTimeout);
      this.debounceTimeout = setTimeout(() => {
        this.getSchools();
      }, 300);
    },
  },

}
</script>

<template>
  <div class="mt-5" >
    <vs-table class="cursor-pointer" >
      <template #header>
        <div  class="text-msj">(Ejemplo de búsqueda: NUESTRA SEÑORA DE FATIMA-<b>IQUITOS</b>). Usa solo un guion para separar el nombre y el distrito, para mejorar la búsqueda.</div>
        <vs-input block class="" primary state="primary"  type="search"    placeholder="Buscar colegio"  v-model="textSearch"
                  @input="searchSchools" />
      </template>

      <template #thead>
        <vs-tr>
          <vs-th>
            Colegio
          </vs-th>
          <vs-th>
            Distrito
          </vs-th>
        </vs-tr>
      </template>
      <template #tbody>
        <vs-tr
            :key="i"
            v-for="(tr, i) in ListSchools"
            :data="tr"
        >
          <vs-td>
            {{ tr.name }}
          </vs-td>
          <vs-td>
            {{ tr.district }}
          </vs-td>
        </vs-tr>
      </template>

      <template #notFound>
        No se encontraron datos. 😢
      </template>
    </vs-table>
  </div>
</template>

<style scoped lang="scss">
.text-msj {
  font-size: 0.6rem;
  color: #950101;
  margin-top: 5px;
  transition: all 0.3s;
}
</style>