<script>
import ImportExcel from "@/views/upload_schools/components/ImportExcel.vue";
import List from "@/views/upload_schools/components/List";

export default {
  components: { List, ImportExcel },

  data() {
    return {};
  },

  computed: {
    schools() {
      return this.$store.getters["uploadSchools/get_school"];
    },
  },

  methods: {
    async getListSchools() {
      await this.$store.dispatch("uploadSchools/getListSchools");
    },
  },

  mounted() {
    this.getListSchools();
  },
};
</script>

<template>
  <div>
    <import-excel />
    <list :list-schools="schools" />
  </div>
</template>
